import React, { useState, useEffect } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import Axios from "../../axios"
import PropTypes from "prop-types"
import Nav from "../components/nav"
import Clients from "../components/clients"
import Footer from "../components/footer"
import Header from "../components/header"
// import contactFormImage from "../images/contact/contact.png"
import BtnArrow from "../images/home/btn-arrow.svg"
import BreadcumHeader from "../components/breadcumHeader"
import Loadable from "@loadable/component"
import "owl.carousel/dist/assets/owl.carousel.css"
import "owl.carousel/dist/assets/owl.theme.default.css"
import { BASE_URL } from "../../config"
const OwlCarousel = Loadable(() => import("react-owl-carousel"))

const query = graphql`
    {
      allStrapiContactPageLogo {
        nodes {
          img {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
        }
      }

    }
`




function Contact(props) {
  
  const data = useStaticQuery(query)
  const contactFormImg = data.allStrapiContactPageLogo.nodes[0]

  // const [ServiceCat, setServiceCat] = useState([])
  // const [contactFormImage, setcontactFormImage] = useState({})
  // useEffect(() => {
  //   async function fetchData() {
  //     const serviceCategories = await Axios.get("/service-categories")
  //     const contactFormImage = await Axios.get("/contact-page-logo")
  //     console.log("contactFormImage", contactFormImage)
  //     setServiceCat(serviceCategories.data)
  //     setcontactFormImage(contactFormImage.data)
  //     return serviceCategories
  //   }
  //   fetchData()
  // }, [])

  const [feedbackName, setFeedbackName] = useState("")
  const [feedbackEmail, setFeedbackEmail] = useState("")
  const [feedbackService, setFeedbackService] = useState("-1")
  const [feedbackDescription, setFeedbackDescription] = useState("")

  const handleFeedbackName = e => {
    setFeedbackName(e.target.value)
  }
  const handleFeedbackEmail = e => {
    setFeedbackEmail(e.target.value)
  }
  const handleFeedbackService = e => {
    setFeedbackService(e.target.value)
  }
  const handleFeedbackDescription = e => {
    setFeedbackDescription(e.target.value)
  }

  const sendFeedback = () => {
    if (!feedbackName || feedbackName == "") {
      alert("Name should not be empty")
      return
    }
    if (!feedbackEmail || feedbackEmail == "") {
      alert("Please enter valid email address")
      return
    }
    if (feedbackService == "-1") {
      setFeedbackService("NOT SPECIFIED")
    }

    const data = {
      name: feedbackName,
      email: feedbackEmail,
      service: feedbackService,
      description: feedbackDescription,
    }

    Axios.post("/messages", data)
      .then(res => {
        alert("send successfully")
        setFeedbackName("")
        setFeedbackEmail("")
        setFeedbackService("")
        setFeedbackDescription("")
      })
      .catch(err => {
        alert("Please enter valid information")
      })
  }

  const validateEmail = email => {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(email)
  }

  const responsive = {
    0: {
      items: 1,
      nav: true,
      loop: true,
    },
  }
  return (
    <div>
      <Nav />
      {/* <Header isHomePage={false} title="Contact Us" /> */}
      <BreadcumHeader title={"Contact Us"} heroImageName={"contact"} />

      <section id="contact-us">
        <section className="container">
          <div className="row my-5">
            <section className="col-md-5 order-2 order-md-1">
              {contactFormImg?.img ? (
                <OwlCarousel
                  className="owl-carousel contact-carousel owl-theme msar-carousel mt-4 mt-md-4"
                  loop
                  nav
                  responsive={responsive}
                  autoplay={true}
                  autoplayTimeout={1000}
                >
                  <div className="item">
                    {/* <img src={contactFormImage} /> */}
                    {/* <img src={`${BASE_URL}${contactFormImage?.img?.url}`} /> */}
                    <img src={contactFormImg.img.localFile.childImageSharp?.original.src} />
                  </div>
                </OwlCarousel>
              ) : null}
            </section>
            <section className="col-md-7 pl-md-5 order-1 order-md-2">
              <div className="full-box">
              <h2 className="font-weight-bold">Send Us A Message</h2>
              <div className="hr mb-4">
                {/* <hr className="hr-msar-dark hr-map " style={{width:`${17  * 20}px`}} /> */}
              </div>
              <div id="frm-contact-us">
                <div className="row">
                  <div className="col-lg-12">
                    <div className="form-group">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Name*"
                        value={feedbackName}
                        onChange={handleFeedbackName}
                        name
                      />
                    </div>
                    <div className="form-group">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Email*"
                        value={feedbackEmail}
                        onChange={handleFeedbackEmail}
                        name
                      />
                    </div>
                    {/* <div className="form-group">
                      <select
                        onChange={handleFeedbackService}
                        value={feedbackService}
                        className="form-control"
                      >
                        <option value="-1">Service Require</option>
                        {ServiceCat.map(document => (
                          <option value={document.name} key={document._id}>
                            {document.name}
                          </option>
                        ))}
                      </select>
                    </div> */}
                    <div className="form-group mb-0">
                      <textarea
                        className="form-control"
                        rows={4}
                        placeholder="Your Text"
                        value={feedbackDescription}
                        onChange={handleFeedbackDescription}
                      />
                    </div>
                    <div className="form-group mt-0 pt-0">
                      <label className="small color-white">
                        * Need to fill marked. Make a call more details
                        required.
                      </label>
                    </div>
                    <div className="button-bor">
                    <button
                      className="btn btn-msar border-1 mb-5 mb-md-0"
                      onClick={sendFeedback}
                    >
                      Send Message <img src={BtnArrow} className="ml-1" />
                    </button>
                    </div>
                  </div>
                </div>
              </div>
              </div>
            </section>
          </div>
        </section>
      </section>

      <Clients />
      <Footer />
    </div>
  )
}

Contact.propTypes = {}

export default Contact
